

import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'


export default {
components:{
Checklist,
XButton,
XHeader,
XInput,
Popup
},
data () {
return {
goods_group:[],
lack_goods_group:[],
showMore:false,
showMenuPop:false,
}
},
methods :{
clickBack () {
this.$vux.confirm.show({
title:"",
content:"?",
onCancel : () => {
this.$vux.confirm.hide();
},
onConfirm : () => {
this.$vux.confirm.hide();
this.$router.replace('/')
}
})
},
clickCommit () {
if (this.lack_goods_group.length == 0) {
return this.$vux.toast.show({
text:"",
time:1000,
type:'warn',
})
}
this.$http.post('/api/lackstore',{
datagroup:this.lack_goods_group
})
.then((res)=>{
this.$vux.toast.show({
text:""
})
this.$router.replace('/');
})
},
getGoodsFromServer () {
this.$http.get('/api/lackstore/template')
.then(({data})=>{
this.goods_group = data.datagroup;
})
},
},
created () {
this.getGoodsFromServer();
}
}



