import axios from 'axios'
import router from '../router'

function startLoading() {    //Element loading-start 
    window.app.$vux.loading.show({
      text:"",
      type:'warn'
    })
}

function endLoading() {    //Element loading-close 
  window.app.$vux.loading.hide();
}

//   header
axios.interceptors.request.use(config => {
    // 
    startLoading()
    if (localStorage.eleToken)
        config.headers.Authorization = "Bearer " + localStorage.eleToken
    return config

}, error => {
    return Promise.reject(error)
})

//   401 token
axios.interceptors.response.use(response => {
    endLoading()
    if (response.data && response.data.errcode > 0) {
        return Promise.reject(response.data);
    }
    return response.data
}, err => {
    // 
    console.log(err)
    endLoading();
    if (err && err.response) {
        switch (err.response.status) {
          case 400: 
            err.message = '(400)';
            if (err.response.data && err.response.data.errmsg) {
                err.message +=  "  " + err.response.data.errmsg;
            }
            break;
          case 401: 
            localStorage.removeItem('eleToken')
            err.message = ',(401)';
            // 
            router.push('/login')
            break;
          case 403: err.message = '(403)'; break;
          case 404: err.message = '(404)'; break;
          case 408: err.message = '(408)'; break;
          case 500: err.message = '(500)'; break;
          case 501: err.message = '(501)'; break;
          case 502: err.message = '(502)'; break;
          case 503: err.message = '(503)'; break;
          case 504: err.message = '(504)'; break;
          case 505: err.message = 'HTTP(505)'; break;
          default: err.message = `(${err.response.status})!`;
        }
      } else {
        err.message = '!'
      }
      window.app.$vux.toast.show({
        text: err.message,
        type:'warn'
      })
      return Promise.reject(err);
})

export default axios


